'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Slider } from '@/components/ui/slider';
import { ClusterAlgorithm, TokenType } from '@/lib/types';
import { Sparkles, Settings2 } from 'lucide-react';

interface ControlPanelProps {
  algorithm: ClusterAlgorithm;
  numClusters: number;
  tokenType: TokenType;
  onAlgorithmChange: (algorithm: ClusterAlgorithm) => void;
  onNumClustersChange: (num: number) => void;
  onTokenTypeChange: (type: TokenType) => void;
  onCluster: () => void;
  disabled?: boolean;
  loading?: boolean;
}

export default function ControlPanel({
  algorithm,
  numClusters,
  tokenType,
  onAlgorithmChange,
  onNumClustersChange,
  onTokenTypeChange,
  onCluster,
  disabled,
  loading,
}: ControlPanelProps) {
  return (
    <div className="bg-white dark:bg-gray-900 rounded-xl border border-gray-200 dark:border-gray-700 p-6 shadow-lg">
      <div className="flex items-center gap-2 mb-6">
        <Settings2 className="w-5 h-5 text-purple-500" />
        <h2 className="text-lg font-semibold text-gray-900 dark:text-gray-100">
          Clustering Controls
        </h2>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {/* Algorithm Selection */}
        <div className="space-y-2">
          <Label htmlFor="algorithm" className="text-sm font-medium text-gray-700 dark:text-gray-300">
            Algorithm
          </Label>
          <Select
            value={algorithm}
            onValueChange={(value) => onAlgorithmChange(value as ClusterAlgorithm)}
            disabled={disabled || loading}
          >
            <SelectTrigger id="algorithm" className="w-full">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="kmeans">K-Means</SelectItem>
              <SelectItem value="hierarchical">Hierarchical</SelectItem>
            </SelectContent>
          </Select>
          <p className="text-xs text-gray-500 dark:text-gray-400">
            {algorithm === 'kmeans' 
              ? 'Fast, partition-based clustering'
              : 'Tree-based, hierarchical clustering'}
          </p>
        </div>

        {/* Token Type Selection */}
        <div className="space-y-2">
          <Label htmlFor="tokenType" className="text-sm font-medium text-gray-700 dark:text-gray-300">
            Token Type
          </Label>
          <Select
            value={tokenType}
            onValueChange={(value) => onTokenTypeChange(value as TokenType)}
            disabled={disabled || loading}
          >
            <SelectTrigger id="tokenType" className="w-full">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="word">Words</SelectItem>
              <SelectItem value="phrase">Phrases</SelectItem>
              <SelectItem value="sentence">Sentences</SelectItem>
            </SelectContent>
          </Select>
          <p className="text-xs text-gray-500 dark:text-gray-400">
            {tokenType === 'word' && 'Individual words'}
            {tokenType === 'phrase' && '2-word combinations'}
            {tokenType === 'sentence' && 'Full sentences'}
          </p>
        </div>

        {/* Number of Clusters */}
        <div className="space-y-2">
          <Label htmlFor="numClusters" className="text-sm font-medium text-gray-700 dark:text-gray-300">
            Number of Clusters: <span className="text-blue-600 dark:text-blue-400 font-bold">{numClusters}</span>
          </Label>
          <Slider
            id="numClusters"
            min={2}
            max={10}
            step={1}
            value={[numClusters]}
            onValueChange={(values) => onNumClustersChange(values[0])}
            disabled={disabled || loading}
            className="mt-2"
          />
          <p className="text-xs text-gray-500 dark:text-gray-400">
            Range: 2-10 clusters
          </p>
        </div>
      </div>

      {/* Cluster Button */}
      <div className="mt-6 flex justify-center">
        <Button
          onClick={onCluster}
          disabled={disabled || loading}
          size="lg"
          className="bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white font-semibold px-8 py-6 text-lg shadow-lg hover:shadow-xl transition-all duration-300 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          {loading ? (
            <>
              <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2" />
              Clustering...
            </>
          ) : (
            <>
              <Sparkles className="w-5 h-5 mr-2" />
              Start Clustering
            </>
          )}
        </Button>
      </div>
    </div>
  );
}
