'use client';

import { useState } from 'react';
import TextInput from '@/components/TextInput';
import ControlPanel from '@/components/ControlPanel';
import ClusterVisualization from '@/components/ClusterVisualization';
import ClusterSummary from '@/components/ClusterSummary';
import { ClusterAlgorithm, TokenType, ClusterResult, ClusterRequest } from '@/lib/types';
import { Sparkles } from 'lucide-react';

export default function Home() {
  const [text, setText] = useState('');
  const [algorithm, setAlgorithm] = useState<ClusterAlgorithm>('kmeans');
  const [numClusters, setNumClusters] = useState(5);
  const [tokenType, setTokenType] = useState<TokenType>('word');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [clusterData, setClusterData] = useState<ClusterResult | null>(null);

  const handleCluster = async () => {
    if (!text.trim()) {
      setError('Please enter some text to cluster');
      return;
    }

    if (text.trim().length < 50) {
      setError('Please enter at least 50 characters for meaningful clustering');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      const request: ClusterRequest = {
        text,
        algorithm,
        numClusters,
        tokenType,
      };

      const response = await fetch('/api/cluster', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(request),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Clustering failed');
      }

      const result: ClusterResult = await response.json();
      setClusterData(result);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred');
      setClusterData(null);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50 to-purple-50 dark:from-gray-950 dark:via-blue-950 dark:to-purple-950">
      {/* Header */}
      <header className="border-b border-gray-200 dark:border-gray-800 bg-white/80 dark:bg-gray-900/80 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-6 py-4">
          <div className="flex items-center gap-3">
            <div className="bg-gradient-to-br from-blue-600 to-purple-600 p-2 rounded-lg">
              <Sparkles className="w-6 h-6 text-white" />
            </div>
            <div>
              <h1 className="text-2xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent">
                Text Clustering Visualizer
              </h1>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                Analyze and visualize text patterns with interactive clustering
              </p>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="container mx-auto px-6 py-8">
        <div className="space-y-6">
          {/* Control Panel */}
          <ControlPanel
            algorithm={algorithm}
            numClusters={numClusters}
            tokenType={tokenType}
            onAlgorithmChange={setAlgorithm}
            onNumClustersChange={setNumClusters}
            onTokenTypeChange={setTokenType}
            onCluster={handleCluster}
            disabled={!text.trim() || loading}
            loading={loading}
          />

          {/* Error Display */}
          {error && (
            <div className="bg-red-50 dark:bg-red-950 border border-red-200 dark:border-red-800 rounded-lg p-4">
              <p className="text-red-700 dark:text-red-300 text-sm font-medium">
                {error}
              </p>
            </div>
          )}

          {/* Main Grid */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Left: Text Input */}
            <div className="bg-white dark:bg-gray-900 rounded-xl border border-gray-200 dark:border-gray-700 p-6 shadow-lg">
              <TextInput
                value={text}
                onChange={setText}
                disabled={loading}
              />
            </div>

            {/* Right: Visualization */}
            <div className="h-[600px]">
              <ClusterVisualization data={clusterData} />
            </div>
          </div>

          {/* Summary Panel */}
          {clusterData && (
            <ClusterSummary data={clusterData} />
          )}
        </div>
      </main>

      {/* Footer */}
      <footer className="border-t border-gray-200 dark:border-gray-800 bg-white/80 dark:bg-gray-900/80 backdrop-blur-sm mt-12">
        <div className="container mx-auto px-6 py-6">
          <p className="text-center text-sm text-gray-600 dark:text-gray-400">
            Built with Next.js, React, and ml-kmeans • Powered by TF-IDF vectorization
          </p>
        </div>
      </footer>
    </div>
  );
}
