'use client';

import { useState } from 'react';
import { Textarea } from '@/components/ui/textarea';
import { Button } from '@/components/ui/button';
import { FileText, Trash2, Sparkles } from 'lucide-react';

interface TextInputProps {
  value: string;
  onChange: (value: string) => void;
  disabled?: boolean;
}

const sampleTexts = [
  {
    title: 'Technology & AI',
    text: `Artificial intelligence and machine learning are transforming industries worldwide. Deep learning neural networks enable computers to recognize patterns in data. Natural language processing allows machines to understand human language. Computer vision helps systems interpret visual information. Robotics and automation are revolutionizing manufacturing. Cloud computing provides scalable infrastructure for AI applications. Data science extracts insights from large datasets. Blockchain technology ensures secure transactions. Internet of things connects devices globally. Cybersecurity protects digital assets from threats. Quantum computing promises exponential processing power. Virtual reality creates immersive experiences. Augmented reality enhances real-world environments.`
  },
  {
    title: 'Indonesian Sample',
    text: `Teknologi informasi berkembang pesat di Indonesia. Kecerdasan buatan mengubah cara kerja perusahaan. Pembelajaran mesin membantu analisis data besar. Komputasi awan menyediakan infrastruktur digital. Keamanan siber melindungi aset digital. Internet of things menghubungkan perangkat pintar. Blockchain menjamin transparansi transaksi. Realitas virtual menciptakan pengalaman imersif. Robotika mengotomasi proses industri. Analitik data menghasilkan wawasan bisnis. Platform digital memfasilitasi perdagangan elektronik. Aplikasi mobile menghadirkan layanan praktis. Startup teknologi mendorong inovasi lokal.`
  }
];

export default function TextInput({ value, onChange, disabled }: TextInputProps) {
  const [charCount, setCharCount] = useState(value.length);
  const wordCount = value.trim().split(/\s+/).filter(w => w.length > 0).length;

  const handleChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const newValue = e.target.value;
    onChange(newValue);
    setCharCount(newValue.length);
  };

  const handleClear = () => {
    onChange('');
    setCharCount(0);
  };

  const handleLoadSample = (text: string) => {
    onChange(text);
    setCharCount(text.length);
  };

  return (
    <div className="flex flex-col gap-4 h-full">
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-2">
          <FileText className="w-5 h-5 text-blue-500" />
          <h2 className="text-lg font-semibold text-gray-900 dark:text-gray-100">
            Input Text
          </h2>
        </div>
        <div className="flex items-center gap-2">
          <span className="text-sm text-gray-500 dark:text-gray-400">
            {wordCount} words • {charCount} chars
          </span>
          {value && (
            <Button
              variant="ghost"
              size="sm"
              onClick={handleClear}
              disabled={disabled}
              className="text-red-500 hover:text-red-600 hover:bg-red-50 dark:hover:bg-red-950"
            >
              <Trash2 className="w-4 h-4" />
            </Button>
          )}
        </div>
      </div>

      <Textarea
        value={value}
        onChange={handleChange}
        disabled={disabled}
        placeholder="Paste your text here for clustering analysis... (minimum 100 characters recommended)"
        className="flex-1 min-h-[300px] resize-none font-mono text-sm bg-white dark:bg-gray-900 border-gray-200 dark:border-gray-700 focus:border-blue-500 dark:focus:border-blue-400 transition-colors"
      />

      <div className="flex flex-wrap gap-2">
        <span className="text-sm text-gray-600 dark:text-gray-400 flex items-center gap-1">
          <Sparkles className="w-4 h-4" />
          Load sample:
        </span>
        {sampleTexts.map((sample, idx) => (
          <Button
            key={idx}
            variant="outline"
            size="sm"
            onClick={() => handleLoadSample(sample.text)}
            disabled={disabled}
            className="text-xs hover:bg-blue-50 dark:hover:bg-blue-950 hover:border-blue-300 dark:hover:border-blue-700 transition-colors"
          >
            {sample.title}
          </Button>
        ))}
      </div>
    </div>
  );
}
