'use client';

import { ClusterResult } from '@/lib/types';
import { BarChart3, Clock, Layers, Download } from 'lucide-react';
import { Button } from '@/components/ui/button';

interface ClusterSummaryProps {
  data: ClusterResult | null;
}

export default function ClusterSummary({ data }: ClusterSummaryProps) {
  if (!data) {
    return null;
  }

  const handleExport = () => {
    const jsonData = JSON.stringify(data, null, 2);
    const blob = new Blob([jsonData], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `clustering-result-${Date.now()}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  return (
    <div className="bg-white dark:bg-gray-900 rounded-xl border border-gray-200 dark:border-gray-700 p-6 shadow-lg">
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center gap-2">
          <BarChart3 className="w-5 h-5 text-cyan-500" />
          <h2 className="text-lg font-semibold text-gray-900 dark:text-gray-100">
            Clustering Summary
          </h2>
        </div>
        <Button
          variant="outline"
          size="sm"
          onClick={handleExport}
          className="hover:bg-cyan-50 dark:hover:bg-cyan-950 hover:border-cyan-300 dark:hover:border-cyan-700 transition-colors"
        >
          <Download className="w-4 h-4 mr-2" />
          Export JSON
        </Button>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
        <div className="bg-gradient-to-br from-blue-50 to-blue-100 dark:from-blue-950 dark:to-blue-900 rounded-lg p-4 border border-blue-200 dark:border-blue-800">
          <div className="flex items-center gap-2 mb-1">
            <Layers className="w-4 h-4 text-blue-600 dark:text-blue-400" />
            <span className="text-xs font-medium text-blue-700 dark:text-blue-300">Clusters</span>
          </div>
          <p className="text-2xl font-bold text-blue-900 dark:text-blue-100">{data.clusters.length}</p>
        </div>

        <div className="bg-gradient-to-br from-purple-50 to-purple-100 dark:from-purple-950 dark:to-purple-900 rounded-lg p-4 border border-purple-200 dark:border-purple-800">
          <div className="flex items-center gap-2 mb-1">
            <BarChart3 className="w-4 h-4 text-purple-600 dark:text-purple-400" />
            <span className="text-xs font-medium text-purple-700 dark:text-purple-300">Total Items</span>
          </div>
          <p className="text-2xl font-bold text-purple-900 dark:text-purple-100">{data.totalTokens}</p>
        </div>

        <div className="bg-gradient-to-br from-cyan-50 to-cyan-100 dark:from-cyan-950 dark:to-cyan-900 rounded-lg p-4 border border-cyan-200 dark:border-cyan-800">
          <div className="flex items-center gap-2 mb-1">
            <Network className="w-4 h-4 text-cyan-600 dark:text-cyan-400" />
            <span className="text-xs font-medium text-cyan-700 dark:text-cyan-300">Connections</span>
          </div>
          <p className="text-2xl font-bold text-cyan-900 dark:text-cyan-100">{data.links.length}</p>
        </div>

        <div className="bg-gradient-to-br from-amber-50 to-amber-100 dark:from-amber-950 dark:to-amber-900 rounded-lg p-4 border border-amber-200 dark:border-amber-800">
          <div className="flex items-center gap-2 mb-1">
            <Clock className="w-4 h-4 text-amber-600 dark:text-amber-400" />
            <span className="text-xs font-medium text-amber-700 dark:text-amber-300">Time</span>
          </div>
          <p className="text-2xl font-bold text-amber-900 dark:text-amber-100">{data.processingTime}ms</p>
        </div>
      </div>

      {/* Cluster Breakdown */}
      <div>
        <h3 className="text-sm font-semibold text-gray-700 dark:text-gray-300 mb-3">
          Cluster Distribution
        </h3>
        <div className="space-y-3">
          {data.clusters.map(cluster => {
            const percentage = ((cluster.size / data.totalTokens) * 100).toFixed(1);
            return (
              <div key={cluster.id} className="space-y-1">
                <div className="flex items-center justify-between text-sm">
                  <div className="flex items-center gap-2">
                    <div 
                      className="w-3 h-3 rounded-full"
                      style={{ backgroundColor: cluster.color }}
                    />
                    <span className="font-medium text-gray-700 dark:text-gray-300">
                      Cluster {cluster.id}
                    </span>
                  </div>
                  <span className="text-gray-500 dark:text-gray-400">
                    {cluster.size} items ({percentage}%)
                  </span>
                </div>
                <div className="h-2 bg-gray-200 dark:bg-gray-700 rounded-full overflow-hidden">
                  <div 
                    className="h-full rounded-full transition-all duration-500"
                    style={{ 
                      width: `${percentage}%`,
                      backgroundColor: cluster.color
                    }}
                  />
                </div>
                {cluster.words.length > 0 && (
                  <div className="flex flex-wrap gap-1.5 mt-2">
                    {cluster.words.slice(0, 8).map((word, idx) => (
                      <div
                        key={idx}
                        className="inline-flex items-center gap-1.5 bg-gray-100 dark:bg-gray-800 rounded-full px-2.5 py-1 border border-gray-200 dark:border-gray-700"
                      >
                        <span className="text-xs font-medium text-gray-700 dark:text-gray-300">
                          {word.text}
                        </span>
                        <span className="text-xs font-bold text-blue-600 dark:text-blue-400">
                          {word.weight.toFixed(2)}
                        </span>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}

function Network({ className }: { className?: string }) {
  return (
    <svg className={className} fill="none" viewBox="0 0 24 24" stroke="currentColor">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 12a9 9 0 01-9 9m9-9a9 9 0 00-9-9m9 9H3m9 9a9 9 0 01-9-9m9 9c1.657 0 3-4.03 3-9s-1.343-9-3-9m0 18c-1.657 0-3-4.03-3-9s1.343-9 3-9m-9 9a9 9 0 019-9" />
    </svg>
  );
}
